// @flow

import React from "react"

import type { Slamoji } from "../data/slamoji-data.js"

type Props = {|
  slamoji: Slamoji,
|}

export default function SlamojiTile(props: Props) {
  const { slamoji } = props
  const src = "/slamoji-images/" + slamoji.filename
  const filenameNoExtension = slamoji.filename.split(".")[0]
  const displayedName = ":" + filenameNoExtension + ":"
  return (
    <div style={styles.container}>
      <img style={styles.img} src={src} alt={slamoji.alt} />
      <div style={styles.label}>{displayedName}</div>
    </div>
  )
}

const styles = {
  container: {
    width: 192,
    height: 128,
    display: "flex",
    flexDirection: "column",
    flexGrow: 0,
    justifyContent: "center",
    alignItems: "center",
    flexShrink: 0,
  },
  img: {
    width: 32,
    height: 32,
  },
  label: {
    fontFamily: "Inconsolata",
    fontSize: 16,
    color: "#21242C64",
    marginTop: 16,
  },
}
